#capture-overlay {
    position: absolute;
    text-align: center;
    padding: 0;
    font-size: 16px;
    background-color: rgba(0, 0, 0, 0.6);
    color: #ffffff;
    transition: all, cubic-bezier(0.23, 1, 0.32, 1), 200ms;
}

#capture-overlay .headers {
    margin-top: 3em;
    margin-left: 3em;
    margin-right: 3em;
}

#capture-overlay h2 {
    font-size: 1em;
}

#capture-overlay .capture-preview img {
    border-radius: 0.5em;
    max-height: 10em;
}